.App {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 2rem;
  background-image: -webkit-gradient(
  linear,
  left top, left bottom,
  from(hsl(240deg 97% 53%)),
  color-stop(9%, hsl(220deg 98% 52%)),
  color-stop(47%, hsl(201deg 99% 51%)),
  color-stop(77%, hsl(181deg 100% 51%)),
  color-stop(91%, hsl(171deg 86% 50%)),
  color-stop(97%, hsl(171deg 57% 51%)),
  color-stop(100%, hsl(171deg 29% 52%)),
  to(hsl(0deg 0% 52%))
);
  background-image: -webkit-linear-gradient(
  top,
  hsl(240deg 97% 53%) 0%,
  hsl(220deg 98% 52%) 9%,
  hsl(201deg 99% 51%) 47%,
  hsl(181deg 100% 51%) 77%,
  hsl(171deg 86% 50%) 91%,
  hsl(171deg 57% 51%) 97%,
  hsl(171deg 29% 52%) 100%,
  hsl(0deg 0% 52%) 100%
);
  background-image: -o-linear-gradient(
  top,
  hsl(240deg 97% 53%) 0%,
  hsl(220deg 98% 52%) 9%,
  hsl(201deg 99% 51%) 47%,
  hsl(181deg 100% 51%) 77%,
  hsl(171deg 86% 50%) 91%,
  hsl(171deg 57% 51%) 97%,
  hsl(171deg 29% 52%) 100%,
  hsl(0deg 0% 52%) 100%
);
  background-image: linear-gradient(
  180deg,
  hsl(240deg 97% 53%) 0%,
  hsl(220deg 98% 52%) 9%,
  hsl(201deg 99% 51%) 47%,
  hsl(181deg 100% 51%) 77%,
  hsl(171deg 86% 50%) 91%,
  hsl(171deg 57% 51%) 97%,
  hsl(171deg 29% 52%) 100%,
  hsl(0deg 0% 52%) 100%
);
  height: calc(100vh - 2rem);


}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



