.Carousel{
    width: 100vw;
    height: 42vh;
    
    overflow-x: scroll;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 20px 50px rgba(255, 255, 255, 0.109) ;
            box-shadow: 0 20px 50px rgba(255, 255, 255, 0.109) ;
}


.Carousel .empty{
    width: 48%;
    height: 72%;
    border-radius: 26px;
    background-color: beige;
    margin: auto auto;
    display: inline-block;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 10px;
    visibility: hidden;
    padding:10px;
}


