.Button{
    width: 80px;
    height: 80px;
    border-radius: 100%;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: auto;
    border: none;
    cursor: pointer;
    

}   
.Container{
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    margin-top: 4rem;
}




